.items {
	overflow: auto;
	height: 100%;
	background: #f8f6f7;
	&__content,
	&__col {
		height: 100%; }
	&__content {
		font-size: 0;
		white-space: nowrap; }
	&__col {
		display: inline-block;
		vertical-align: top;
		width: 422px;
		min-width: 30vw; //36.192
		white-space: normal; } }

.item {
	position: relative;
	height: 100%;
	font-family: $futura;
	&.is-active {
		.item {
			&__img {
				height: 240px; }
			&__control {
				span {
					transform: rotate(45deg); } } } }
	&__img {
		width: 100%;
		height: 100%;
		@include cover;
		@include gpu;
		transition: height 0.4s; }
	&__container {
		position: absolute;
		left: 30px;
		right: 30px;
		bottom: 49px;
		display: flex;
		flex-direction: column;
		padding: 40px 0 20px;
		border-radius: 5px;
		max-height: calc(100% - 239px);
		background: $white;
		box-shadow: 0 40px 34px -20px rgba(0,0,0,0.13); }
	&__control {
		position: absolute;
		top: 0;
		left: 50%;
		border: 12px solid rgba($white, 0.2);
		border-radius: 50%;
		width: 80px;
		height: 80px;
		background-color: $blue;
		background-clip: padding-box;
		transform: translate3d(-50%,-50%,0);
		span {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -11px 0 0 -11px;
			width: 22px;
			height: 22px;
			transition: transform 0.4s;
			&:before,
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate3d(-50%,-50%,0); }
			&:before {
				border-top: 2px solid $white;
				width: 100%; }
			&:after {
				border-left: 2px solid $white;
				height: 100%; } } }
	&__title {
		padding: 0 74px 12px;
		color: $dark;
		@include f(24px, 500, 1);
		text-align: center; }
	&__content {
		display: none;
		overflow: auto;
		flex-grow: 1;
		padding: 30px 50px 20px; }
	&__slider {
		overflow: auto;
		padding: 0 50px;
		margin: 0 -50px 20px;
		font-size: 0;
		white-space: nowrap; }
	&__slide {
		display: inline-block;
		vertical-align: top;
		&:not(:last-child) {
			margin-right: 10px; }
		&-in {
			border-radius: 5px;
			width: 182px;
			height: 111px; } }
	&__description {
		overflow: auto;
		margin-bottom: 19px;
		height: 4em*1.5;
		@include f(20px, 500, 1.5); }
	&__price {
		padding: 17px 10px 20px;
		margin-bottom: 30px;
		border-top: 1px solid #D5D5D5;
		border-bottom: 1px solid #D5D5D5;
		@include f(26px, 500, 1);
		text-align: center;
		span {
			color: $green; } }
	&__btn {
		&:not(:last-child) {
			margin-bottom: 13px; } } }
