body {
	min-width: 320px;
	color: $dark;
	font-family: $futura;
	@include f(16px, 500, 1.25);
	@include font-smoothing;
	background: $white; }

.out {
	@include clr; }

.main {
	overflow: auto;
	height: 100vh;
	@include md {
		padding-top: 100px; }
	@include sm {
		padding-top: 50px; } }

// titles
.title-lg,
h1 {
	@include f(34px, 500, 1.25);
	font-family: $futura; }
.title-md,
h2 {
	@include f(26px, 500, 1.25);
	font-family: $futura; }
.title-sm,
h3 {
	@include f(24px, 500, 1.25); }
.title-xs,
h4 {
	@include f(20px, 700, 1.5);
	letter-spacing: 0.09em; }

// color block
.text-green {
	color: $green; }
