.btn-nav {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  width: 84px;
  height: 100px;
  background: none;
}

@media only screen and (max-width: 1023px) {
  .btn-nav {
    display: block;
  }
}

@media only screen and (max-width: 767px) {
  .btn-nav {
    width: 42px;
    height: 50px;
  }
}

.btn-nav.is-active:before {
  transform: translate3d(4px, 0, 0) rotate(45deg);
}

@media only screen and (max-width: 767px) {
  .btn-nav.is-active:before {
    transform: translate3d(2px, 0, 0) rotate(45deg);
  }
}

.btn-nav.is-active:after {
  transform: translate3d(4px, 0, 0) rotate(-45deg);
}

@media only screen and (max-width: 767px) {
  .btn-nav.is-active:after {
    transform: translate3d(2px, 0, 0) rotate(-45deg);
  }
}

.btn-nav.is-active .btn-nav__inner {
  opacity: 0;
  transform: scaleX(0);
}

.btn-nav:before, .btn-nav:after, .btn-nav__inner {
  position: absolute;
  left: 50%;
  margin-left: -12px;
  border-radius: 2.5px;
  width: 24px;
  height: 2px;
  background: #fff;
}

@media only screen and (max-width: 767px) {
  .btn-nav:before, .btn-nav:after, .btn-nav__inner {
    margin-left: -6px;
    border-radius: 1px;
    width: 12px;
    height: 1px;
  }
}

.btn-nav:before, .btn-nav:after {
  content: '';
  transition: transform 0.3s;
}

.btn-nav:before {
  top: 39px;
  transform-origin: left top;
}

@media only screen and (max-width: 767px) {
  .btn-nav:before {
    top: 19px;
  }
}

.btn-nav:after {
  top: 55px;
  transform-origin: left bottom;
}

@media only screen and (max-width: 767px) {
  .btn-nav:after {
    top: 27px;
  }
}

.btn-nav__inner {
  top: 47px;
  transition: opacity 0.3s, transform 0.3s;
}

@media only screen and (max-width: 767px) {
  .btn-nav__inner {
    top: 23px;
  }
}

