}

.item.is-active .item__control span {
  transform: rotate(45deg);
}

.item__img {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  transform: translate3d(0, 0, 0);
  transition: height 0.4s;
}

.item__container {
  position: absolute;
  left: 30px;
  right: 30px;
  bottom: 49px;
  display: flex;
  flex-direction: column;
  padding: 40px 0 20px;
  border-radius: 5px;
  max-height: calc(100% - 239px);
  background: #fff;
  box-shadow: 0 40px 34px -20px rgba(0, 0, 0, 0.13);
}

.item__control {
  position: absolute;
  top: 0;
  left: 50%;
  border: 12px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  width: 80px;
  height: 80px;
  background-color: #66A6FF;
  background-clip: padding-box;
  transform: translate3d(-50%, -50%, 0);
}

.item__control span {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -11px 0 0 -11px;
  width: 22px;
  height: 22px;
  transition: transform 0.4s;
}

.item__control span:before, .item__control span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.item__control span:before {
  border-top: 2px solid #fff;
  width: 100%;
}

.item__control span:after {
  border-left: 2px solid #fff;
  height: 100%;
}

